<template>
  <div class="searchWrap">
    <header>
      <router-link to="/msite">
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <div>搜索</div>
    </header>
    <div class="form">
      <input
        type="search"
        placeholder="请输入商家或美食名称"
        class="search_input"
        v-model="searchInput"
        v-on:input="change"
      />
      <input type="submit" name="submit" class="search_submit" @click="searchBtn" />
    </div>
    <section v-if="isShow">
      <h4 class="h4title">商家</h4>
      <ul class="list_container">
        <li v-for="s in shopArr" class="list_li" :key="s.id" @click="goshop(s.id,s.image_path,s.name)">
          <section class="item_left">
            <img :src="imgurl + s.image_path" alt class="restaurant_img" />
          </section>
          <section class="item_right">
            <div>
              <p>
                <span>{{s.name}}</span>
                <i class="el-icon-circle-plus"></i>
              </p>
              <p>月售{{s.recent_order_num}}单</p>
              <p>{{s.float_delivery_fee}}元起送 / 距离{{s.distance}}</p>
            </div>
            <ul class="item_right_detail"></ul>
          </section>
        </li>
      </ul>
    </section>
    <section class="search_history" v-if="isShow1">
      <h4 class="title_restaurant">搜索历史</h4>
      <ul>
        <li class="history_list" v-for="(h,index) in searchNameArr" :key="index">
          <span class="history_text ellipsis">{{h}}</span>
          <i class="el-icon-close" @click="deleteOne"></i>
        </li>
      </ul>
      <footer class="clear_history" @click="clearHis">清空搜索历史</footer>
    </section>
    <section class="footer">
      <section class="guide_item" @click="gomsite">
        <i class="el-icon-eleme"></i>
        <span>外卖</span>
      </section>
      <section class="guide_item">
        <i class="el-icon-search"></i>
        <span>搜索</span>
      </section>
      <section class="guide_item" @click="goorder">
        <i class="el-icon-tickets"></i>
        <span>订单</span>
      </section>
      <section class="guide_item" @click="goprofile">
        <i class="el-icon-user"></i>
        <span>我的</span>
      </section>
    </section>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      searchInput: "",
      cityGeohash: "",
      shopArr: [],
      imgurl: "https://elm.cangdu.org/img/",
      searchNameArr: [],
      isShow1: true,
      isShow: false
    };
  },
  created() {
    let placeNameHistory = localStorage.getItem("placeNameHistory");
    let hisArr = JSON.parse(localStorage.getItem("placeHistory"));
    let newArr = hisArr.filter(v => {
      return v.name == placeNameHistory;
    });
    this.cityGeohash = newArr[0].geohash;
    if (localStorage.getItem("searchName")) {
      this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
    } else {
      this.isShow1 = false;
    }
  },
  methods: {
    searchBtn(p) {
      this.isShow = true;
      this.isShow1 = false;
      this.axios
        .get("v4/restaurants", {
          params: {
            geohash: this.cityGeohash,
            keyword: this.searchInput
          }
        })
        .then(res => {
          this.shopArr = res;
        })
        .catch(err => {});
      //历史记录
      if (!localStorage.getItem("searchName")) {
        let hisArr = [this.searchInput];
        localStorage.setItem("searchName", JSON.stringify(hisArr));
      } else {
        let hisArr = JSON.parse(localStorage.getItem("searchName"));
        // 去重
        let newArr = hisArr.filter(v => {
          return v == this.searchInput;
        });
        if (!newArr.length) {
          hisArr.push(this.searchInput);
          localStorage.setItem("searchName", JSON.stringify(hisArr));
        }
      }
    },
    clearHis() {
      localStorage.removeItem("searchName");
      this.isShow1 = false;
    },
    change() {
      if (this.searchInput) {
        this.isShow1 = false;
      } else {
        this.isShow = false;
        this.isShow1 = true;
        if (localStorage.getItem("searchName")) {
          this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
        } else {
          this.isShow1 = false;
        }
      }
    },
    deleteOne(num) {
      const data = JSON.parse(localStorage.getItem("searchName"));
      data.splice(num, 1);
      if (data.length == 0) {
          localStorage.removeItem("searchName")
      }else{
          localStorage.setItem("searchName", JSON.stringify(data));
      }
    
      if (localStorage.getItem("searchName")) {
        this.searchNameArr = JSON.parse(localStorage.getItem("searchName"));
      } else {
        this.isShow1 = false;
      }
    },
    goprofile(){
           this.$router.push("/profile")
      },
      goorder(){
            this.$router.push('/order')
        },
      gomsite(){
            this.$router.push('/msite')
        },
        goshop(s,i,n){
          this.$router.push('/shop');
          localStorage.setItem("shopId",s);
          localStorage.setItem("shopImg",i);
          localStorage.setItem("shopName",n)
        }
  }
};
</script>

<style scoped>
.el-icon-search{
    color: #3190e8;
}
.searchWrap {
  padding-top: 1.95rem;
}
a {
  text-decoration: none;
}
header > a {
  position: absolute;
  left: 0.4rem;
  color: #fff;
  font-size: 1rem;
  top: 50%;
  transform: translateY(-50%);
  text-decoration: none;
}
header > div {
  width: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  color: #fff;
  text-align: center;
  font-size: 0.8rem;
  font-weight: 700;
}
.footer {
  background-color: #fff;
  position: fixed;
  z-index: 100;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 1.95rem;
  display: flex;
}
.guide_item {
  flex: 1;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
}
.guide_item > span {
  font-size: 0.45rem;
  color: #666;
  text-align: center;
}
.guide_item > i {
  margin-top: 0.3rem;
  fill: #ccc;
}
.form {
  background-color: #fff;
  padding: 0.5rem;
  display: flex;
}
.search_input {
  flex: 4;
  border: 0.025rem solid #e4e4e4;
  font-size: 0.65rem;
  color: #333;
  border-radius: 0.125rem;
  background-color: #f2f2f2;
  font-weight: 700;
  padding: 0 0.25rem;
  height: 1.5rem;
  outline: none;
}
.search_submit {
  flex: 1;
  border: 0.025rem solid #3190e8;
  margin-left: 0.2rem;
  font-size: 0.65rem;
  color: #fff;
  border-radius: 0.125rem;
  background-color: #3190e8;
  font-weight: 700;
  padding: 0 0.25rem;
  height: 1.5rem;
  outline: none;
}
.h4title {
  font-size: 0.6rem;
  line-height: 2rem;
  text-indent: 0.5rem;
  font-weight: 700;
  color: #666;
}
.list_container {
  background-color: #fff;
}
.list_li {
  display: flex;
  justify-content: "center";
  padding: 0.5rem;
  border-bottom: 0.025rem solid #e4e4e4;
}
.restaurant_img {
  width: 1.7rem;
  height: 1.7rem;
}
.item_left {
  margin-right: 0.25rem;
}
.item_right {
  font-size: 0.55rem;
  flex: 1;
}
.item_right p {
  line-height: 0.9rem;
}
.item_right_detail {
  margin-top: 0.25rem;
}
.title_restaurant {
  font-size: 0.6rem;
  line-height: 2rem;
  text-indent: 0.5rem;
  font-weight: 700;
  color: #666;
}
.history_text {
  display: inline-block;
  width: 80%;
}
.clear_history {
  background-color: #fff;
  color: #3190e8;
  font: 0.7rem/2rem Microsoft YaHei;
  font-weight: 700;
  text-align: center;
}
.el-icon-close {
  font: 0.7rem/2rem Microsoft YaHei;
  stroke: rgb(153, 153, 153);
}
.history_list {
  background-color: #fff;
  border-bottom: 0.025rem solid #e4e4e4;
  font: 0.7rem/2rem Microsoft YaHei;
  padding: 0 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>